"use client"

import { useState, useEffect } from "react"
import { Sidebar } from "@/components/sidebar"
import { Dashboard } from "@/components/dashboard"
import { ConnectionForm } from "@/components/connection-form"
import { TableMappingView } from "@/components/table-mapping"
import { DataTypeRules } from "@/components/data-type-rules"
import { MigrationPanel } from "@/components/migration-panel"
import { NewProjectDialog } from "@/components/new-project-dialog"
import { LoginPage } from "@/components/login-page"
import { SettingsDialog, defaultSettings } from "@/components/settings-dialog"
import { mockProjects } from "@/lib/mock-data"
import type { Project, ConnectionConfig, User, AppSettings } from "@/lib/types"

export default function HomePage() {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  const [projects, setProjects] = useState<Project[]>(mockProjects)
  const [selectedProjectId, setSelectedProjectId] = useState<string | null>("1")
  const [activeTab, setActiveTab] = useState("dashboard")
  const [newProjectOpen, setNewProjectOpen] = useState(false)

  const [settingsOpen, setSettingsOpen] = useState(false)
  const [appSettings, setAppSettings] = useState<AppSettings>(defaultSettings)

  const selectedProject = projects.find((p) => p.id === selectedProjectId) || null

  // Check for existing session on mount
  useEffect(() => {
    const savedUser = localStorage.getItem("ora2pg_user")
    if (savedUser) {
      setUser(JSON.parse(savedUser))
    }
    setIsLoading(false)
  }, [])

  const handleLogin = (loggedInUser: User) => {
    setUser(loggedInUser)
    localStorage.setItem("ora2pg_user", JSON.stringify(loggedInUser))
  }

  const handleLogout = () => {
    setUser(null)
    localStorage.removeItem("ora2pg_user")
  }

  const handleNewProject = (name: string, description: string) => {
    const newProject: Project = {
      id: Date.now().toString(),
      name,
      description,
      createdAt: new Date(),
      updatedAt: new Date(),
      tableMappings: [],
      status: "draft",
    }
    setProjects([...projects, newProject])
    setSelectedProjectId(newProject.id)
  }

  const handleSaveSource = (config: ConnectionConfig) => {
    if (!selectedProjectId) return
    setProjects(
      projects.map((p) => (p.id === selectedProjectId ? { ...p, sourceConnection: config, updatedAt: new Date() } : p)),
    )
  }

  const handleSaveTarget = (config: ConnectionConfig) => {
    if (!selectedProjectId) return
    setProjects(
      projects.map((p) => (p.id === selectedProjectId ? { ...p, targetConnection: config, updatedAt: new Date() } : p)),
    )
  }

  const renderContent = () => {
    switch (activeTab) {
      case "dashboard":
        return <Dashboard project={selectedProject} />
      case "connections":
        return (
          <ConnectionForm
            sourceConnection={selectedProject?.sourceConnection}
            targetConnection={selectedProject?.targetConnection}
            onSaveSource={handleSaveSource}
            onSaveTarget={handleSaveTarget}
          />
        )
      case "tables":
        return <TableMappingView />
      case "datatypes":
        return <DataTypeRules />
      case "migration":
        return <MigrationPanel />
      default:
        return <Dashboard project={selectedProject} />
    }
  }

  // Show loading state
  if (isLoading) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="animate-pulse text-muted-foreground">Loading...</div>
      </div>
    )
  }

  if (!user) {
    return <LoginPage onLogin={handleLogin} />
  }

  return (
    <div className="flex h-screen bg-background">
      <Sidebar
        projects={projects}
        selectedProjectId={selectedProjectId}
        onSelectProject={setSelectedProjectId}
        onNewProject={() => setNewProjectOpen(true)}
        activeTab={activeTab}
        onTabChange={setActiveTab}
        user={user}
        onLogout={handleLogout}
        onOpenSettings={() => setSettingsOpen(true)}
      />
      <main className="flex-1 overflow-auto">{renderContent()}</main>

      <NewProjectDialog open={newProjectOpen} onOpenChange={setNewProjectOpen} onCreateProject={handleNewProject} />

      <SettingsDialog
        open={settingsOpen}
        onOpenChange={setSettingsOpen}
        settings={appSettings}
        onSaveSettings={setAppSettings}
      />
    </div>
  )
}
